import { CST } from "../CST";
import { MenuScene } from "./MenuScene";
export class LoadScene extends Phaser.Scene{
    constructor(){
        super({
            key: CST.SCENES.LOAD
        })
    }
    init(){

    }
    preload(){
        // change screen resolution to: 800x600

        //load image, spritesheet, sound

        this.load.image("title_bg", "./assets/title_bg.jpg");

        this.load.image("options_button", "./assets/options_button.png");

        this.load.image("play_button", "./assets/play_button.png");

        this.load.image("logo", "./assets/logo.png");

        this.load.spritesheet("cat", "./assets/cat.png", {
            frameHeight: 32,
            frameWidth: 32
        });

        this.load.audio("title_music", "./assets/shuinvy-childhood.mp3");

        //create loading bar

        let loadnigBar = this.add.graphics({
            fillStyle: {
                color: 0xffffff //White
            }
        })

        /*
        Loader Events:
            complete - when loading everything
            progress - loader number progress in decimal
        */

       //smiulate large load
       /*
       for(let i=0; i<100; i++){
            this.load.spritesheet("cat" + i, "./assets/cat.png", {
                frameHeight: 32,
                frameWidth: 32
            });
       }*/

        this.load.on("progress",(percent)=>{
            loadnigBar.fillRect(0, this.game.renderer.height / 2, this.game.renderer.width * percent, 50);
            console.log(percent);
        })

        this.load.on("complete", ()=>{
            console.log("done")
        })

    }
    create(){
        this.scene.add(CST.SCENES.MENU, MenuScene, false)
        this.scene.start(CST.SCENES.MENU,"hello from LoadScene");
    }
}